export const MapContainer = (level) => {
    const { kakao } = window;
  
    //   div
    const container = document.getElementById("map");
    const options = {
      center: new kakao.maps.LatLng(37.553836, 126.969652),
      level: 4
    };
  
    //   
    return new kakao.maps.Map(container, options);
  };